<template>
  <div id="goodsDetails">
    <navheader></navheader>
    <search-navbar></search-navbar>
    <div class="goodsDetails-top">
      <div class="" v-for="(item,index) in goodsDetailsTop">
        <div
          :class="{'topBottom':index === num}"
          @click="tab(index)"
        >
          {{item.text}}
        </div>
      </div>
    </div>


    <div class="goodsDetails-center">
      <div class="goodsInfo" v-for="(item,index) in  goodsDetailsCenter">
        <div class="goodsInfo-img" v-show="index==0&&index==num">
          <div class="goodsInfo-img-top">
            <img :src="goodsImg" alt="" >
          </div>
          <div class="goodsInfo-img-center">
            <div class="center-title">
              <span class="title-text">&nbsp;&nbsp;{{title}}</span>
            </div>
            <div class="center-price">
              <span></span>
              <span>:</span>
              <span style="color:firebrick;font-size: 22px">{{price}}</span>
              <span></span>
            </div>
            <div class="center-gg">
              <span></span><span>{{gg}}</span>
            </div>
            <div class="center-address">
              <span>:</span>
              <span>{{isprovince}}</span>&nbsp;
              <span>{{iscity}}</span>&nbsp;
              &nbsp;<i class="iconfont icon-dizhi" @click="changeAddress"></i>
            </div>
          </div>
        </div>
        <div class="goodsInfo-ge" v-show="index==1&&index==num">
          <div class="goodsInfo-ge-top">
            <div v-for="(item,index) in goodsInfoGeTop"
                 :class="{'fontColor':index === num1}" @click="tab1(index)">
              {{item.text}}
            </div>
          </div>
          <div class="goodsInfo-ge-bottom"
               v-for="(item,index) in goodsInfoGeBottom">
            <div class="ge-bottom-one" v-show="index==0&&index==num1">
              <div class="ge-bottom-img" v-for="(item,index) in productImg">
                <img :src="item.pimg" alt="" class="">
              </div>
            </div>
            <div class="ge-bottom-two" v-show="index==1&&index==num1">
              <div class="ge-bottom-two-tr" v-for="(item,index) in details">
                <div class="">
                  <span></span>
                  <span>:</span>
                  <span>{{item.pp}}</span>
                </div>
                <div class="">
                  <span></span>
                  <span>:</span>
                  <span>{{item.bzq}}</span>
                </div>
                <div class="">
                  <span></span>
                  <span>:</span>
                  <span>{{item.jhl}}</span>
                </div>
                <div class="">
                  <span></span>
                  <span>:</span>
                  <span>{{item.tj}}</span>
                </div>
              </div>
            </div>
            <div class="ge-bottom-three" v-show="index==2&&index==num1">
              <div class="">
                <p class=""></p>
                <div class="">+1-2+1++</div>
              </div>
              <div class="">
                <p class=""></p>
                <div class="">1<br>
                  48100<br>
                  2<br>
                  YC-fresh<br>
                  400-626-3311<br>
                  09:00-24:00<br>
                  <br>
                  <br>
                </div>
              </div>
              <div class="">
                <p class=""></p>
                <div class="">1<br>2<br>3<br>4<br>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="goodsInfo-pl" v-show="index==2&&index==num">
          2
        </div>
      </div>
    </div>


    <div class="goodsDetails-bottom">
      <div class="bottom-kf">
        <i class="iconfont icon-kefu"></i>
        <i class="iconfont icon-heart" @click="collection" ref="Collection"></i>
      </div>
      <div class="bottom-addCart" @click="addCart">
        
      </div>
      <div class="bottom-NowBuy" @click="nowBuy">
        
      </div>
    </div>



    <modal
      :msg="message"
      :isHideModal="HideModal">
    </modal>
  </div>
</template>

<script type="text/ecmascript-6">

  import Navheader from "../../bese/navheader/navheader.vue";
  import SearchNavbar from "../../bese/searchNavbar/seachNavbar.vue";
  import Modal from '../../bese/modal/modal.vue';
  import {addCart} from '../../api/config'


  import axios from 'axios';

  export default {
    name: 'goodsDetails',
    components: {
      SearchNavbar,
      Navheader,
      Modal
    },
    data() {
      return {
        goodsDetailsTop: [{text: ""}, {text: ""}, {text: ""}],
        goodsDetailsCenter: [{}, {}, {}],
        goodsInfoGeTop: [{text: ""}, {text: ""}, {text: ""}],
        goodsInfoGeBottom: [{}, {}, {}],
        num: 0,
        num1: 0,
        message: "",
        HideModal: true,
        goodsInfo: [],
        productImg: [],
        details: [],
        goodsImg: '',
        title: "",
        price: "",
        img:'',
        gg: "",



        isprovince: '',
        iscity: '',

      }

    },
    computed: {
      swiper() {
        return this.$refs.mySwiper.swiper
      }
    },
    created() {
      this._getGoodsDetails();

    },
    methods: {
      _getGoodsDetails() {
        axios.get('/api/MobileGoodsDetails',{
          params: {
            id: this.$route.query.id,
            index:this.$route.query.index
          }
        })
          .then((res) => {

            this.goodsImg = res.data.img;
            this.title = res.data.title;
            this.price = res.data.price;
            this.gg = res.data.number;
            this.productImg = res.data.productImg;
            this.details = res.data.details;







          })
          .catch((err) => {
            console.log(err)
          })
      },
      onSelected(data) {
        this.isprovince = data.province.value;
        this.iscity = data.city.value;
        this.isarea = data.area.value;
      },
      changeAddress(){

      },
      tab(index) {
        this.num = index;
      },
      tab1(index) {
        this.num1 = index;
      },
      collection() {
        if (this.$refs.Collection.className === 'iconfont icon-heart') {
          this.$refs.Collection.className = 'iconfont icon-heart red'
        }
        else if (this.$refs.Collection.className === 'iconfont icon-heart red') {
          this.$refs.Collection.className = 'iconfont icon-heart '
        }
      },
      addCart() {
        let title = this.title;
        let price = this.price;
        let id = this.$route.query.id;
        let index = this.$route.query.index;
        let img  = this.goodsImg;
        addCart(index, img, title, price, id);

        this.message = "";
        this.HideModal = false;
        const that = this;

        function a() {
          that.message = "";
          that.HideModal = true
        }

        setTimeout(a, 2000);
      },
      nowBuy() {
        alert('hh')
      }
    },

  }
</script>
<style scoped lang="less" rel="stylesheet/less">
  @import "../../common/less/base";

  .goodsDetails-top {
    height: 50px;
    background-color: @color-F0;
    display: flex;
    > div {
      flex: 1;
      display: flex;
      align-items: center;
      justify-content: center;
      > div {
        height: 100%;
        width: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: @font-size-large;
        border: 1px solid @color-F0;
      }
    }
    .topBottom {
      border-bottom: 1px solid @color-red;
    }

  }

  .goodsDetails-center {
    .goodsInfo {
      .goodsInfo-img {
        height: 620px;
        .goodsInfo-img-top {
          height: 375px;
          img {
            width: 100%;
            height: 375px;
          }
        }
        .goodsInfo-img-center {
          height: 200px;
          .center-title {
            height: 50px;
            display: flex;
            align-items: center;
            justify-content: center;
            font-size: @font-size-large-x;
            color: @color-green;

          }
          .center-price {
            height: 50px;
            display: flex;
            align-items: center;
            padding-left: 20px;
            font-size: @font-size-medium-x;

          }
          .center-gg {
            height: 50px;
            display: flex;
            align-items: center;
            padding-left: 20px;
            font-size: @font-size-medium-x;

          }
          .center-address {
            height: 50px;
            display: flex;
            align-items: center;
            padding-left: 20px;
            font-size: @font-size-medium;
            .icon-dizhi {
              font-size: @font-size-large-x;
              color: @color-red;
            }
          }
        }
      }
      .goodsInfo-ge {
        .goodsInfo-ge-top {
          display: flex;
          align-items: center;
          div {
            height: 40px;
            border-top: 3px solid @color-white;
            border-bottom: 1px solid @color-white;
            flex: 1;

            display: flex;
            background-color: @color-yellow;
            align-items: center;
            justify-content: center;

          }
          .fontColor {
            color: @color-red;
          }
        }
        .goodsInfo-ge-bottom {
          .ge-bottom-one {
            .ge-bottom-img {
              width: 95%;
              margin: 10px auto 60px auto;
              img {
                width: 100%;
              }
            }
          }
          .ge-bottom-two {
            .ge-bottom-two-tr {
              width: 95%;
              margin: 20px auto;
              border-right: 1px solid @color-F0;
              border-left: 1px solid @color-F0;
              div {
                display: flex;
                margin: 10px auto;
                border: 1px solid @color-F0;
                span:first-child {
                  flex: 5;
                  display: flex;
                  align-items: center;
                  justify-content: center;
                }
                span:nth-child(1) {
                  flex: 1;
                  display: flex;
                  align-items: center;
                  justify-content: center;
                }
                span:last-child {
                  flex: 5;
                  display: flex;
                  align-items: center;
                  justify-content: center;
                }
              }

            }

          }
          .ge-bottom-three {
            margin-bottom: 60px;
            div {
              width: 95%;
              margin: 10px auto;
              background-color: @color-F0;
            }
          }

        }
      }
    }
  }

  .goodsDetails-bottom {
    max-width: 640px;
    width: 100%;
    height: 50px;
    margin: 0 auto;
    background-color: @color-F0;
    position: fixed;
    bottom: 0;
    z-index: 99;
    display: flex;
    .bottom-kf {
      flex: 3;
      display: flex;
      align-items: center;
      i {
        flex: 1;
        display: flex;
        align-items: center;
        justify-content: center;
      }
      .icon-kefu {
        font-size: @font-size-large;
        color: @color-dialog-background;
      }
      .icon-heart {
        font-size: @font-size-large;
        color: @color-dialog-background;
      }
      .red {
        color: @color-red;
      }

    }
    .bottom-addCart {
      flex: 2;
      display: flex;
      align-items: center;
      justify-content: center;
      background-color: @color-theme;
      color: @color-white;
      font-size: @font-size-medium-x;
    }
    .bottom-NowBuy {
      flex: 2;
      display: flex;
      align-items: center;
      justify-content: center;
      background-color: @color-red;
      color: @color-white;
      font-size: @font-size-medium-x;
    }
  }


</style>



